.sql-flow {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .sql-flow-tool {
    padding: 0 20px;
    .ant-tabs-nav {
      margin-bottom: 0;
    }
  }
  .react-json-view {
    height: 0;
    flex: 1;
    overflow: auto;
    padding: 20px;
  }
}

.x6-graph,
.sql-flow-view {
  width: 100%;
  height: 100%;
}

.app-container {
  display: flex;
  height: 100%;
  margin: 20px;
  border: 1px solid #ccc;

  .flow-wrapper {
    height: 100%;
    position: relative;
    overflow: hidden;
    outline: none !important;
    flex-grow: 1;

    #table-flow {
      position: relative;
      // 调大width目的是暂时解决节点拖动到table-flow区域外时(如flow-wrapper)节点宽度自动变窄的问题
      width: 1000%;
      height: 100%;

      .auxiliary-line-x {
        position: absolute;
        border: 0.5px dashed #2ab1e8;
        z-index: 9999;
      }

      .auxiliary-line-y {
        position: absolute;
        border: 0.5px dashed #2ab1e8;
        z-index: 9999;
      }
    }
  }
}

@primary-color: #7f56d9;@border-radius-base: 6px;