.code-editor {
  height: 100%;
  display: flex;
  flex-direction: column;
  .code-editor-tool {
    height: 46px;
    display: flex;
    background-color: #7f56d9;
    align-items: center;
    padding: 0 20px 0 50px;
  }
  .ace_editor {
    flex: 1;
    height: 0;
    .highlight-marker {
      position: absolute;
      background-color: rgb(255, 0, 43);
      z-index: 20;
    }
  }
}

@primary-color: #7f56d9;@border-radius-base: 6px;